import { notFound } from "next/navigation";
import path from "path";
import Markdown from "react-markdown";
import fs from "fs";

export default async function ContentPage({
  params,
}: {
  params: { content?: string[] };
}) {
  console.log("AAAAAAAAA " + params.slug);

  // Check if slug exists and has at least one element.
  if (!params.slug || params.slug.length === 0) {
    return notFound();
  }

  // Determine folder name and file name from URL segments.
  const folder = params.slug[0];
  // If a child item is specified, use it; otherwise, assume a standalone file.
  const fileName = params.slug.length > 1 ? params.slug[1] : params.slug[0];

  // const filePath = path.join(
  //   process.cwd(),
  //   "public",
  //   "md",
  //   folder,
  //   `${fileName}.md`
  // );

  const filePath = path.join(
    process.cwd(),
    "public",
    "md",
    "algorithms",
    "algorithms.md"
  );

  console.log("TTTTTTTTTT " + filePath);

  if (!fs.existsSync(filePath)) {
    return notFound();
  }

  const fileContents = fs.readFileSync(filePath, "utf8");

  return (
    <Markdown className="markdown-content mx-auto p-4">{fileContents}</Markdown>
  );
}
